@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&display=swap);
:root {
  --primary-color: #5991ff;
  --primary-color__hover: #4e80e3;
  --primary-color__active: #3f68b7;
  --primary-gradient-start-color: #b6cbff;
  --primary-background-color: #202c40;
  --positive-color: #61bf8a;
  --positive-background-color: #074823;
  --negative-color: #ff8e8e;
  --negative-background-color: #5a2f2f;
  --shadow-color-1: rgb(80 80 80/15%);
  --shadow-color-2: rgb(255 255 255/33%);
  --text1-color: #fff;
  --text2-color: #e1e1e1;
  --text3-color: #bcbcbc;
  --text4-color: #757575;
  --text5-color: #353535;
  --ui-bg-color: #11111d;
  --ui-disabled-color: #ededed;
  --ui-bg-1-color: #1a1c29;
  --ui-bg-1-color__hover: #2a2d3e;
  --ui-bg-1-color__active: #272a39;
  --ui-loading-color: #343748;
  --ui-loading-color__highlight: #343748;
  --box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.05);
  --font-family: "Josefin Sans", "San Serif";


}
body {
font-family: "Josefin Sans", "San Serif";
font-family: var(--font-family);

margin: 0 auto;
padding: 0 0.5rem;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
background-color: #11111d;
background-color: var(--ui-bg-color);
color: #fff;
color: var(--text1-color);
}

 h1{
  font-size: 2em;
  color: #fff;
  color: var(--text1-color);
 }
 h2{
  font-size: 1.5em;
  color: #e1e1e1;
  color: var(--text2-color);
 }
 h3{
  font-size: 1.3em;
  color: #bcbcbc;
  color: var(--text3-color);
 }
 h4{
  font-size: 1em;
  color: #757575;
  color: var(--text4-color);
 }
 h5{
  font-size: .8em;
  color: #353535;
  color: var(--text5-color);
 }
 h6{
  font-size: .7em;
  color: #fff;
  color: var(--text1-color);
 }

 p{
  color: #353535;
  color: var(--text5-color);
 }





  
  .bitcoin{
    padding-top:80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color:#888888;}
  
  .path {
    stroke-dasharray: 2110;
    stroke-dashoffset: 2110;
    stroke-width:2;
    stroke-linecap:round;
    animation: dash 6s linear infinite;
    fill-opacity:0;
    stroke:orange;
  }
  
  @keyframes dash {
    0% {
      stroke-dashoffset: 2110;
      opacity:0;
      stroke:orange;
    }
    15%{
      opacity:1;
      stroke:orange;
    }
    70%{
      opacity:1;
      stroke:orange;
    }
    100%{
      stroke-dashoffset:0;
      opacity:0;
      stroke:yellow;
    }
  }
